header {
  height: 13vh;
  background-color: rgba(0, 0, 0, 0.750);
  padding: 1vh;  

  .header {
    height: 100%;
    display: flex;
    padding: 2vh 3vw;
    border-radius: 15px;
    border: 1px solid rgb(255, 255, 255);

    .header__logo {
      background: url('../super_ferret.jpg') no-repeat;
      background-size: cover;
      // height: 11vh;
      // height: 100%;
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
    }

    .header__title {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 3vw;
      // height: 100%;
      color: rgb(255, 255, 255);
      margin: 0 2vw;
    }
  }
}